@import url(https://fonts.googleapis.com/css?family=Lalezar|Montserrat|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

/*dashboard layout utilizing code by 
https://medium.com/better-programming/build-a-responsive-modern-dashboard-layout-with-css-grid-and-flexbox-bd343776a97e
https://www.quackit.com/css/grid/tutorial/create_a_nested_grid.cfm
*/

/* overall style modifier */
  body {
    margin: 0;
    padding: 0;
    color: #fff;
    font-family: 'Open Sans', Helvetica, sans-serif;
    box-sizing: border-box;
    background-color: #989898ff;
  }

  h1 {
    font-family: 'Lalezar', cursive;
    font-size: 45px;
  }

  h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 19px;
  }

  h3 {
      font-family: 'Roboto', sans-serif;
      font-size: 24px;
      font-display: bold;
  }

  h4 {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
  }


 /* Primary page for the content */
  .primary-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 1fr 0px;
    grid-gap: 8px;
    grid-template-areas:
      "header"
      "body"
      "footer";
    height: 100vh;
  }

  .header {
    grid-area: header;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0559ba;
  }

  .body {
    grid-area: body;
    background-color: #989898ff;
  }

  .footer {
    grid-area: footer;
    background-color: #0559ba;
  }


  /* Header Bar */
  .logo {
    width: 70px;
    height: 70px;
    padding: 10px;
    margin: 0px;
    -webkit-transform: translate(0px, 2.5px);
            transform: translate(0px, 2.5px);
    float: left;
  }

  .title {
    margin: 0px;
    padding: 0px;
    float: left;
    overflow: hidden;
  }

  .navitemset {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  .navitem {
    padding: 0px 20px 0px 20px;
    float: left;
    display: inline;
  }

 

  /* Specific-Posting Cards */
  .specificposting-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas:
      "aptinfo gallery gallery"
      "aptinfo gallery gallery"
      "contact map review"
      "visitation map review"
    ;
    margin: 0px 10px 0px 10px;
    grid-gap: 10px;
    height: calc(100vh - 100px);
  }

  .aptinfo {
    grid-row-start: aptinfo-start;
    grid-row-end: aptinfo-end;

    overflow: auto;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }

  .contact {
    grid-area: contact;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }

  .visitation {
    grid-area: visitation;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }
  
  .gallery {
    grid-column-start: gallery-start;
    grid-column-end: gallery-end;
    grid-row-start: gallery-start;
    grid-row-end: gallery-end;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }

  .map {
    grid-row-start: map-start;
    grid-row-end: map-end;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }

  .review {
    grid-row-start: review-start;
    grid-row-end: review-end;

    padding: 0px 10px 0px 10px;
    background-color: #e3e4e6;
    color: black;
  }
